* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  margin: 30px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

form {
  align-items: center;
  display: flex;
}

input[type="text"], select {
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-right: 10px;
  padding: 10px;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
}

button:hover {
  background-color: #0056b3;
}

.js-list {
  flex-wrap: wrap;
  gap: 25px;
  padding: 0;
  list-style: none;
  display: flex;
}

.js-list li {
  width: 200px;
  padding: 10px;
}

.js-list img {
  width: 40px;
  height: 40px;
  margin-right: 10px;
}

.js-list p {
  margin: 0;
  font-weight: 400;
}

.js-list h2 {
  color: #007bff;
  margin: 0 20px 0 0;
  font-weight: 500;
}

.js-list h3 {
  margin: 5px 0;
  font-weight: 500;
}

